<template>
  <div class="dialog" :style="positionStyle">
    <transition name="fade">
      <div class="mask" v-if="!nomask && show" @click="onMask" ref="mask"></div>
    </transition>
    <transition :name="animateName" @after-leave="afterLeave">
      <div class="scroll-area" v-if="show" ref="content"></div>
    </transition>
  </div>
</template>

<script>
/**
 * position[]: 'middle' || 'top' || 'right' || 'bottom' || 'left'
 * animateName[]:'middle' || 'top' || 'right' || 'bottom' || 'left'
 * nomask[]:true:false:
 */
export default {
  props: {
    position: {
      type: String,
      default: 'middle',
    },
    animateName: {
      type: String,
      default: 'middle',
    },
    nomask: {
      type: Boolean,
      default: false,
    },
    noClose: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      show: false, //
    };
  },
  computed: {
    positionStyle() {
      const result = {
        justifyContent: 'center',
        alignItems: 'center',
      };
      if (this.position.includes('left')) {
        result.alignItems = 'flex-start';
      }
      if (this.position.includes('right')) {
        result.alignItems = 'flex-end';
      }
      if (this.position.includes('top')) {
        result.justifyContent = 'flex-start';
      }
      if (this.position.includes('bottom')) {
        result.justifyContent = 'flex-end';
      }
      return result;
    },
  },
  mounted() {
    this.show = true;
  },
  methods: {
    onMask(e) {
      if (this.noClose && e.target && e.target === this.$refs.mask) {
        console.log('');
        return;
      }
      this.onClose();
    },
    onClose() {
      // 
      this.show = false;
    },
    afterLeave: function () {
      // 
      // console.log('afterleave');
      // console.log(this.show);
      this.$close();
    },
  },
};
</script>

<style lang="less" scoped>
@import './index.less';
</style>
